import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, LoadingController } from '@ionic/angular';
import { ApicallService } from 'src/app/services/apicall.service';
import { GlobalService } from 'src/app/services/global.service';
import { ToastService } from 'src/app/services/toast.service';
@Component({
  selector: 'app-manage-doctor',
  templateUrl: './manage-doctor.page.html',
  styleUrls: ['./manage-doctor.page.scss'],
})
export class ManageDoctorPage implements OnInit {

 public edit:any=
    { name:'', qualification:'', number:'' ,route:''}

    public filter_data:any;
    public Doctor:any ;
    public get_doc: any ;
    public allCities: any ;
    public allRout: any ;
    public getdoctor : any = {city:'', rout:''}
  constructor(private router:Router , public global:GlobalService , public apicall : ApicallService, public toast : ToastService, private loadingCtrl: LoadingController,    private alertController: AlertController) { }

  ngOnInit() {
    this.get_doctor();
    this.getAllCities();
    this.getAlldoctorrout();
  }

  async getAllCities(){
    await this.apicall.api_getCities();
    this.global.GetCities.subscribe(res =>{
      this.allCities = res;
    })
  }

  async getAlldoctorrout(){
    this.global.GetRout.subscribe(res =>{
      this.allRout = res;
    })
  }

  
 async get_doctor(){
  await this.apicall.api_getDoctor();
  this.global.Doctor.subscribe(res =>{
    if(res == ''){
      this.toast.docnotFound();
    }
    else{
      this.Doctor = res;
    }
  })
  }
 async selectCity(event){
    console.log(event.target.value);
    await this.apicall.api_getDoctorrout(event.target.value);
    if(event.target.value == 'all'){
      await this.apicall.api_getDoctor();
    }
    else{
      this.getdoctor.city = event.target.value;
      await this.apicall.api_getdoctorbyAlltype(this.getdoctor);
    }
  }
 async selectroute(event){
    console.log(event.target.value);
      this.getdoctor.rout = event.target.value;
      await this.apicall.api_getdoctorbyAlltype(this.getdoctor);
  }


  add_doctor(){
    this.router.navigate(['add-doctor'])
  }

 async delete(d_id){
    const x = {d_id:d_id};
  await this.apicall.api_deletedoctor(x)
 await this.get_doctor();
  }

  update(data){
    console.log(data); 
    this.showLoading();
    setTimeout(() => {
      this.router.navigate(['/add-doctor'], { state: { data: data} })
    }, 1000);
  }


  async showLoading() {
    const loading = await this.loadingCtrl.create({
      message: 'Loading...',
      duration: 1000,
      spinner: 'circles',
    });

    loading.present();
  }

  async DeleteAlert(d_id) {
    const alert = await this.alertController.create({
        header: 'Are you sure to delete this Doctor!',
        buttons: [
            {
                text: 'Cancel',
                role: 'cancel',
                handler: () => {}
            }, {
                text: 'OK',
                role: 'confirm',
                handler: () => {
                  console.log(d_id);
                  this.delete(d_id);
                }
            },
        ]
    });

    await alert.present();

    const {role} = await alert.onDidDismiss();
}
  
}
